.advantages {
  background-color: $color-ghost-white;
}

.advantages__wrapper {
  padding-bottom: 100px;
}

.advantages__title {
  text-align: center;
  margin-bottom: 80px;

  @include vp-767 {
    margin-bottom: 43px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 240px 1fr;
  column-gap: 60px;

  @include vp-1199 {
    grid-template-columns: minmax(min-content, max-content) minmax(
        min-content,
        max-content
      );
    column-gap: 10px;
    justify-content: space-between;
    row-gap: 42px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.advantages__list-item {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  min-height: 165px;
  padding: 0 11px 26px 20px;
  border-top: 2px solid #d10a4220;

  &:nth-child(-n + 2) {
    align-items: flex-start;
    border-top: none;

    @include vp-767 {
      align-items: center;
    }
  }

  @include vp-1199 {
    justify-content: flex-start;
    align-items: flex-start;
    min-height: 76px;
    padding: 0;
    border-top: none;
  }

  @include vp-767 {
    align-items: center;
    flex-direction: column;
  }
}

.advantages__list-item b {
  font-size: 71px;
  line-height: 71px;
  margin-right: 30px;
  background-image: linear-gradient(127.61deg, #d10a42 0%, #0041ca 45.58%);
  background-clip: text;
  -webkit-text-fill-color: transparent;

  @include vp-1199 {
    font-size: 58px;
    line-height: 58px;
  }

  @include vp-767 {
    text-align: center;
    margin-right: 0;
  }
}

.advantages__list-item span {
  display: block;
  font-size: 14px;
  line-height: 21px;
  color: #2e35ac;

  @include vp-1199 {
    font-size: 14px;
    line-height: 21px;
  }

  @include vp-767 {
    text-align: center;
  }
}

.advantages__list-item h3 {
  font-size: 22px;
  line-height: 33px;
  margin: 0 0 3px;

  @include vp-1199 {
    font-size: 20px;
    line-height: 30px;
  }

  @include vp-767 {
    text-align: center;
  }
}

.advantages__list-item p {
  font-size: 16px;
  line-height: 22px;
  margin: 0;

  @include vp-767 {
    text-align: center;
  }
}

.advantages__list-item-main {
  display: flex;
  grid-column: 2 / 3;
  grid-row: 1 / 3;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 307px;
  padding: 53px 34px 58px;
  color: $color-default-white;
  background-color: $color-resolution-blue;

  @include vp-1199 {
    grid-column: 1 / 3;
    grid-row: 2 / 3;
    min-height: 239px;
    margin: 0 118px;
  }

  @include vp-767 {
    grid-column: 1 / 2;
    grid-row: 3 / 4;
    margin: 0;
  }
}

.advantages__list-item-main b {
  font-size: 74px;
  line-height: 74px;
  margin-bottom: 9px;
  margin-right: 0;
  -webkit-text-fill-color: $color-default-white;
}

.advantages__list-item-main h3 {
  font-size: 26px;
  line-height: 26px;
  margin: 0 0 39px;
}

.advantages__list-item-main p {
  text-align: center;
  margin: 0;
}
